body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Styling of the fixed top bar in the app */

.navbar {
	overflow: hidden; /* Don't allow scrolling */
	display: block; /* Occupies its own horizontal space */
	background-color: $primary-color;
	position: fixed !important; /* Doesn't move */
	top: 0;
	width: 100%; /* 100% of width of screen */
	align-content: center;
	border-radius: 0px;
	padding: 0px;
	z-index: 100; /* Force it to be on top */
}
  
.navbar-title {
	color: white;
	font-size: 18px;
}

/* navbarItems defines styling of items within navbar. They should be centered top-to-bottom
with a small internal margin */
.navbar-items {
	display: inline-flex;
	align-items: center;
	padding-top: 5px;
	padding-bottom: 5px;
}
  
  /* sidemenu -- Styling of the side navigation menu */
.sidemenu {
	height: 100%; /* Menu is full height of window */
	width: 0; /* Menu hidden -- will be opened on click */
	position: fixed; /* Menu stays in place regardless of other content */
	z-index: 150; /* Force the menu to be at front */
	top: 48px; /* Position menu 48 px from top of screen; accomodates top navbar */
	left: 0; /* Position the menu on the left hand side */
	background-color: #13294E; /* Background color is dark blue */
	overflow-x: hidden; /* Don't allow horizontal scroll */
	transition: 0.5s; /* Animation transition time is 0.5 sec */
	padding-top: 60px; /* Place menu content 60 px from top */

	a {
		padding: 16px 8px 16px 32px;
		text-decoration: none;
		font-size: 16px;
		color: white;
		display: block;
		transition: 0.3s;
		&:visited, &:active {
			background-color: #13294E !important;
			color: white !important;
		}
	}

	div {
		padding: 0px 0px 10px 6px;
		text-decoration: none;
		font-size: 16px;
		color: white;
		display: block;
		transition: 0.3s;
		/* border-bottom: 2px solid white; */
	}
}
  

/* Cascading style within a style: This definition applies only to sidenavtitle class defined in an
element that's a child of sidenav element. */
.sidemenu .sidemenu-title {
	position: absolute;
	width:100%;
	top: 0px;
	left: 0px;
	padding: 0px 0px 0px 6px;
	font-size: 18px;
	margin-top: 20px;
	margin-bottom: 20px !important;
	border-bottom: 2px solid white;
}

.sidemenu .sidemenu-userID {
	font-size: 14px;
}

/* side menu button styling */
.sidemenu-btn {
	background-color: transparent;
	border-color: #13294E;
	text-align: center;
}

.sidemenu-btn-icon {
	color: white;
	font-size: 30px;
	cursor: pointer;
	text-align: center;
}

.sidemenu-item {
	color: white !important;
}

.sidemenu-item:hover {
	cursor: default;
	text-decoration: none;
	color: #13294E !important;
	background-color: #F0F0F0;
}

/* side menu styling when menu item selected */
/* side menu styling when menu item selected */
.sidemenu-item-selected {
	pointer-events: none;
	cursor: default;
	text-decoration: none;
	color: #13294E !important;
	background-color: #F0F0F0;
}

/* The SpeedScore dark blue color theme */
.btn-color-theme {
	background-color: #13294E;
	color: white;
}

/* The color theme when a bottom bar menu item is selected */
.btn-alt-color-theme {
	background-color: #F0F0F0;
	color: #13294E;
}

/* change items to gray when disabled class is applied */
.disabled {
	color: gray;
}

.btn-color-theme-outline {
	border: 2px;
	border-color: #13294E;
	color: #13294E;
}

/* Styling for the divs in the app. Ensures that the content at top isn't obscured by top bar */
.padded-page {
	height: 100%; /* 100% of app */
	padding: 56px 8px 64px 8px;
}

.hide {
	display:none;
}

.clickable-div{
	cursor: pointer;
}

/* Styling for mode bar at the bottom of the page */
.modebar {
	background-color: #13294E;
	overflow: hidden;
	position: fixed;
	bottom: 0;
	width: 100%;
	z-index: 100;
	
	/* Style the links inside the bottom nav bar */
	a {
		display: block;
		float: left;
		width: 32%;
		color: white;
		text-align: center;
		padding: 2px 2px;
		text-decoration: none;
		font-size: 17px;

		/* Add a gray background color to the button when it is selected */
		&:active {
			background-color: #F0F0F0;
			color: #13294E;
		}

		/* Add a gray background color to button when it is hovered */
		&:hover {
			color: #F0F0F0;
		}
	}
}

/* Hide the link that should open and close the navbar on small screens */
.modebar .icon {
	color: white;
	display: none;
}

/* Style the icons in the bottom bar */
.modebar-icon {
	display:block;
	color: white;

	padding-top:3px;
}

/* Style the text in the bottom bar */
.modebar-text {
	display:block;
	font-size: small;
	color: white;
}

body {
	margin: 0;
}

/* This class disables a button */

.disabled-button {
	pointer-events: none;
	opacity: 0.4;
}

/* Style the floating action button, which is fixed in the lower right of the app */
.floatbtn {
	position:fixed;
	width:60px;
	height:60px;
	bottom:60px;
	right:40px;
	background-color:#13294E;
	color:white;
	border-radius:50px;
	text-align:center;
	box-shadow: 2px 2px 3px #999;

	&-icon {
	color: white;
	margin-top:22px;
	}

	a:visited, a:active {
		color: white !important;
	}
}

/* Form styling */
.form-center {
	text-align: center;
}

.visible {
	display: block;
}
	
/* Make an item invisible */
.invisible {
	display: none;
}

.sidemenu-open {
	width: 250px;
}
	
/* set width to 0px when menu is closed */
.sidemenu-closed {
	width: 0px;
}